import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import logo from '../img/phanorcoll-small-logo.png';
import GatsbyLogo from "../img/gatsby-logo.svg";
require("prismjs/themes/prism-twilight.css");
import '../scss/index.scss';
let AOS;
const Header = () => (
  <div>
    <header className="header">
      <div className="logo">
        <img src={logo} alt='Phanor Coll fullstack developer' />
        <Link to='/'>phanor coll</Link>
      </div>

      <input className="menu-btn" type="checkbox" id="menu-btn" />
      <label className="menu-icon" htmlFor="menu-btn"><span className="navicon"></span></label>
      <ul className="menu">
        <li><Link to="/">Home</Link></li>
        <li><Link to="/about">About</Link></li>
        <li><Link to="/projects">Projects</Link></li>
        <li><Link to="/contact">Contact</Link></li>
      </ul>
    </header>
  </div>
)

class TemplateWrapper extends Component {


  componentDidMount() {
    if (typeof window !== `undefined`) {
      AOS = require('aos');
      AOS.init();
    }

  }


  render() {
    return (
      <div>
        <Helmet
          title={this.props.data.site.siteMetadata.title}
          meta={[
            { name: 'description', content: 'Sample' },
            { name: 'keywords', content: 'sample, something' },
          ]}
        />
        <Header />
        <div>
          {this.props.children()}
        </div>
        <footer>
          <div className="text-center pc-footer-logo">
            <img src={logo} />
          </div>
          <div className="text-center">
            © 2017 <Link to={this.props.data.site.siteMetadata.homepage}>{this.props.data.site.siteMetadata.author}</Link>. All Rights Reserved. Made with <i className="fa fa-heart"></i> on <i className="fa fa-linux"></i> using <Link to="https://www.gatsbyjs.org">Gatsbyjs</Link>
          </div>
          <div>
            <ul className="text-center pc-social-icons icon-circle icon-zoom pc-list-unstyled pc-list-inline">
              <li> <Link to="https://github.com/phanorcoll"><i className="fa fa-github"></i></Link> </li>
              <li> <Link to="https://twitter.com/phanor/"><i className="fa fa-twitter"></i></Link> </li>
              <li> <Link to="https://www.linkedin.com/in/phanorcoll/"><i className="fa fa-linkedin"></i></Link> </li>
              <li> <Link to="https://www.youtube.com/c/PhanorColl"><i className="fa fa-youtube"></i></Link> </li>
              <li> <a href="http://phanorcoll.deviantart.com/"><i className="fa fa-deviantart"></i></a> </li>
            </ul>
          </div>
        </footer>
      </div>
    )
  }
}

TemplateWrapper.propTypes = {
  children: PropTypes.func,
}

export default TemplateWrapper

export const pageQuery = graphql`
  query LayoutIndexQuery {
    site {
      siteMetadata {
        author
        homepage
        title
      }
    }
  }
  `



